.modal-drawer{
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    height: 100vh;
    width: 100%;
    background: #efefef;
}

.slide-in-left-enter {
    transform: translateX(-100%);
  }
  
.slide-in-left-enter-active {
    transform: translateX(0);
    opacity: 1;
    transition: all ease 1000ms;
}
  
.slide-in-left-exit {
    transform: translateX(0%);
    opacity: 1;
}
  
.slide-in-left-exit-active {
    transform: translateX(-100%);
    opacity: 0;
    transition: all ease 1000ms;
}